
import { computed, defineComponent, ref } from 'vue'
import { PageSearch } from '@/components/page-search'
import { contentTableConfig } from './config/content.config'
import PageContent from '@/components/page-content'
import { useStore } from '@/store'
import { Md5 } from 'ts-md5/dist/md5'
import { ElMessage, ElMessageBox } from 'element-plus'
export default defineComponent({
  name: 'users',
  components: {
    PageSearch,
    PageContent
  },
  setup() {
    const pageName = 'users'
    const store = useStore()
    // 编辑
    const dialogVisible = ref(false)
    // 表单数据
    const formData = ref({
      id: 0,
      username: '',
      realname: '',
      cellphone: '',
      statuts: ''
    })
    const options = [
      { title: '启用', value: 'true' },
      { title: '禁用', value: 'false' }
    ]
    const handleEditData = (row: any) => {
      dialogVisible.value = true
      formData.value.id = row.id
      formData.value.username = row.username
      formData.value.realname = row.realname
      formData.value.cellphone = row.cellphone
      formData.value.statuts = row.statuts
    }

    const editConfirmHandle = () => {
      dialogVisible.value = false
      store.dispatch('system/editPageDataAction', { formData, pageName })
    }
    //分配角色
    let userId = 0
    let rolesOptions: any[] = []
    let roleIds = ref<number[]>([])
    const dialogVisibleRole = ref(false)
    const handleRoleClick = async (row: any) => {
      dialogVisibleRole.value = true
      userId = row.id
      const list = await store.dispatch('system/getRoleAction')
      list.forEach((role: any) => {
        rolesOptions.push({ name: role.name, value: role.id })
      })
      row.roles.forEach((role: any) => {
        roleIds.value.push(role.id)
        console.log(roleIds.value)
      })
    }

    const handleAssignRole = async () => {
      dialogVisibleRole.value = false
      const roleIdsLength = roleIds.value.length
      const res = await store.dispatch('system/assignRoleAction', {
        roleIds,
        userId,
        roleIdsLength
      })
      if (!res.success) {
        return ElMessage({
          showClose: true,
          message: '编辑失败',
          type: 'error'
        })
      }
      ElMessage({
        showClose: true,
        message: '编辑成功',
        type: 'success'
      })
      store.dispatch('system/getRoleAction')
    }
    // 关闭时清空选中避免叠加
    const handleClose = () => {
      rolesOptions = []
      roleIds.value = []
    }

    // 添加用户

    const dialogVisibleUser = ref(false)
    const formDataUser = ref({
      username: '',
      realname: '',
      cellphone: '',
      statuts: '',
      email: '',
      password: ''
    })
    const clearForm = () => {
      formDataUser.value.username = ''
      formDataUser.value.realname = ''
      formDataUser.value.cellphone = ''
      formDataUser.value.statuts = ''
      formDataUser.value.email = ''
      formDataUser.value.password = ''
    }
    const addUser = () => {
      dialogVisibleUser.value = true
    }
    const addUserHandler = async () => {
      dialogVisibleUser.value = false
      const newPassord = Md5.hashStr(formDataUser.value.password)
      formDataUser.value.password = newPassord
      const res = await store.dispatch('system/addUserAction', { formDataUser })
      if (!res.success) {
        return ElMessage({
          showClose: true,
          message: '添加失败',
          type: 'error'
        })
      }
      ElMessage({
        showClose: true,
        message: '添加成功',
        type: 'success'
      })
    }

    // 删除用户
    const handleDeleteData = (row: any) => {
      const id = row.id
      ElMessageBox.confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const res = await store.dispatch('system/deleteDataAction', { id, pageName })
          if (!res.success) {
            return ElMessage({
              type: 'warning',
              message: '删除失败'
            })
          }
          ElMessage({
            type: 'success',
            message: '删除成功!'
          })
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '已取消删除'
          })
        })
    }

    // 分页
    const pageInfo = ref({
      pageNum: 1,
      pageSize: 6
    })
    const queryInfo = pageInfo.value
    const total = computed(() => store.getters[`system/pageListCount`](pageName))
    console.log(total.value)
    const handleSizeChange = (pageSize: any) => {
      pageInfo.value.pageSize = pageSize
      console.log(queryInfo)
      store.dispatch('system/getPageListAction', {
        pageName,
        queryInfo
      })
    }
    const handleCurrentChange = (pageNum: any) => {
      pageInfo.value.pageNum = pageNum
      console.log(queryInfo)
      store.dispatch('system/getPageListAction', {
        pageName,
        queryInfo
      })
    }
    return {
      contentTableConfig,
      handleEditData,
      dialogVisible,
      formData,
      options,
      editConfirmHandle,
      handleRoleClick,
      dialogVisibleRole,
      roleIds,
      rolesOptions,
      handleAssignRole,
      handleClose,
      addUser,
      formDataUser,
      dialogVisibleUser,
      addUserHandler,
      handleDeleteData,
      handleSizeChange,
      handleCurrentChange,
      total,
      pageInfo,
      clearForm
    }
  }
})
